import React, { useState, useEffect } from "react";
import store from "store";

import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Row, Col, Button, Input, Form } from "reactstrap";
import { TiTimes, TiInputChecked, TiHeart } from "react-icons/ti";

const storeKey = "Shopping-key";

function App() {
  const [item, setItem] = useState({ name: "", quantity: "" });
  const [menu, setMenu] = useState([]);

  //Storing early data to localstorage
  useEffect(() => {
    if (typeof store.get(storeKey) !== undefined) {
      setMenu(store.get(storeKey).data);
    }
  }, []);

  useEffect(() => {
    store.set(storeKey, { data: menu });
  }, [menu]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setItem({ ...item, [name]: value });
  };

  //Value adding
  const addItem = (event) => {
    event.preventDefault();
    if (item.name !== "") {
      setMenu([
        ...menu,
        {
          id: Date.now(),
          value: item.name,
          quantity: item.quantity,
          status: "none",
        },
      ]);
      setItem({ name: "", quantity: "" });
    }
  };

  //Check Items
  const crossItem = (id) => {
    setMenu(
      menu.filter((obj) => {
        if (obj.id === id) {
          obj.status = obj.status === "none" ? "line-through" : "none";
        }
        return obj;
      })
    );
  };

  //Delete Items
  const delItem = (id) => {
    setMenu(menu.filter((value) => value.id !== id));
  };

  return (
    <Container>

      {/* Input Forms */}
      <Form onSubmit={addItem} className="sticky-top bg-white">
        <Row>
          <Col className="text-center mt-4">
            <h2>Shopping list</h2>
          </Col>
        </Row>
        <Row>
          <Col xs="8" md={{ size: 5, offset: 2 }}>
            <Input
              name="name"
              type="text"
              placeholder="Item"
              value={item.name}
              onChange={handleChange}
              autoComplete="off"
            />
          </Col>
          <Col xs="4" md="3">
            <Input
              name="quantity"
              type="text"
              placeholder="Qty"
              value={item.quantity}
              onChange={handleChange}
            />
          </Col>
        </Row>
        <Row>
          <Col className="text-center m-2 ">
            <Button
              className="m-1"
              color="danger"
              onClick={() => {
                setMenu([]);
              }}
            >
              Clear list
            </Button>
            <Button
              type="submit"
              className="m-1"
              color="primary"
              onClick={addItem}
            >
              Add Item
            </Button>
          </Col>
        </Row>
      </Form>
      <Row>
        <Col>
          {/* Shopping List Section*/}
          {menu.map((todo, index) => (
            <Button
              className="m-1"
              id={todo.id}
              key={index}
              color="primary"
              outline 
              style={{ textDecoration: todo.status }}
            >
              {/* Cross Button */}
              <TiInputChecked
                className="mx-1"
                color="green"
                onClick={() => crossItem(todo.id)}
              />

              | {todo.value}
              {todo.quantity !== "" ? ": " : ""}
              {todo.quantity} |

              {/* Delete Button */}
              <TiTimes
                className="mx-1"
                onClick={() => delItem(todo.id)}
                color="red"
              />
            </Button>
          ))}
        </Col>
      </Row>

      {/* Footer Section */}
      <Row>
        <span className="text-center fixed-bottom bg-white ">
          Made with <TiHeart color="red" /> by{" "}
          <a
            href="https://github.com/zahidmuhammed"
            style={{ textDecoration: "none", color: "GrayText" }}
          >
            Zahid
          </a>
        </span>
      </Row>
    </Container>
  );
}

export default App;
